import Vue from "vue";
import Router from "vue-router";
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "index",
      component: () => import("@/page/index")
    },
    {
      path: "/rule",
      name: "rule",
      component: () => import("@/page/rule")
    },
    {
      path: "/red_envelope",
      name: "red_envelope",
      component: () => import("@/page/red_envelope")
    },
    {
      path: "/lottery",
      name: "lottery",
      component: () => import("@/page/lottery.vue"),
    }
  ]
});



